nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color:#2F4F4F;
   
}


nav .title {
    font-size: 1.5rem;
    margin: 1rem;
    font-weight: bold;
    text-decoration: none;
    color: aliceblue;
}


nav .text {
   font-family: fantasy;
    color: aliceblue;
}


nav ul {
    display: flex ;
}

nav ul li {
    list-style: none;
   
}

nav ul li a {
    display: block;
    text-decoration: none;
    color: aliceblue;
    padding: 0.5rem;
    margin: 0 0.5rem;
    border-radius: 0.5rem;
}

